import React from "react";
import skills from "./right-body-data/skill-list";
import hobbies from "./right-body-data/hobby-list";
import { Grid, Icon } from "semantic-ui-react";

function SkillRow(props) {
  const name = props.name;
  const stars = [];
  for (let i = 0; i < props.stars; i++) {
    stars.push(<Icon key={i} name={"star"} color="yellow" />);
  }
  return (
    <Grid>
    <Grid.Row>
      <Grid.Column width={16}>
        <p>
          <b>{name}</b>: {stars}
        </p>
      </Grid.Column>
    </Grid.Row></Grid>
  );
}

class RightBody extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      shortTerm:
        "- Ngn hn: Tch lu kin thc, kinh nghim, lm quen vi mi trng pht trin phn mm thc t, thnh tho cng vic trong thi gian thc tp",
      longTerm:
        "- Di hn: Tr thnh full-stack developer, nm vng chuyn mn ca mt vi ngn ng ch o. "
    };
  }

  render() {
    const skillList = skills;
    const hobbyList = hobbies;
    return (
      <Grid>
        <Grid.Row style={{ marginTop: 30 }}>
          <Grid.Column width={16}>
            <h4>MC TIU NGH NGHIP</h4>
            <hr
              style={{
                height: 2,
                border: "none",
                color: "#333",
                backgroundColor: "#333"
              }}
            />
            <Grid.Row style={{ marginTop: 25 }}>
              <Grid.Column width={16}>
                <p>{this.state.shortTerm}</p>
                <p>{this.state.longTerm}</p>
              </Grid.Column>
            </Grid.Row>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row style={{ marginTop: 30 }}>
          <Grid.Column width={16}>
            <h4>K NNG</h4>
            <hr
              style={{
                height: 2,
                border: "none",
                color: "#333",
                backgroundColor: "#333"
              }}
            />
            <Grid.Row style={{ marginTop: 25 }}>
              <Grid.Column width={16}>
                {skillList.map((skill, index) => {
                  return <SkillRow key={index} name={skill.name} stars={skill.stars} />;
                })}
              </Grid.Column>
            </Grid.Row>
          </Grid.Column>
        </Grid.Row>
        <Grid.Row style={{ marginTop: 30 }}>
          <Grid.Column width={16}>
            <h4>S THCH</h4>
            <hr
              style={{
                height: 2,
                border: "none",
                color: "#333",
                backgroundColor: "#333"
              }}
            />
            <Grid.Row style={{ marginTop: 25 }}>
              <Grid.Column width={16}>
                {hobbyList.map((hobby, index) => {
                  return <li key={index}>{hobby}</li>;
                })}
              </Grid.Column>
            </Grid.Row>
          </Grid.Column>
        </Grid.Row>
      </Grid>
    );
  }
}

export default RightBody;
